<template>
  <span :class="'jconfont jcon' + evelIcon(name)"></span>
</template>

<script>
import icons from "./json.js";
export default {
  name: "jqIcon",
  props: {
    name: {
      type: String,
      default: "yooxi",
    },
  },
  data() {
    return {
      icons: icons.glyphs,
    };
  },
  created() {},
  methods: {
    evelIcon(name) {
      const _icons = this.icons.map((item) => item.font_class);
      const _j = _icons.includes(name);
      return _j ? name : "yooxi";
    },
  },
};
</script>

